	module.exports = {"data":{"markdownRemark":{"html":"<h2>Intent</h2>\n<p>This step is to introduce the first design principles to the problem. SOLID has been chosen as an approachable and somewhat widely practiced principle. </p>\n<p>There are varying opinions on SOLID programming and concerns over it's validity in the programming community, if you have a different principle set you would like to include in this program, feel free to substitute it in this step while including the extension below.</p>\n<p>The below resources are not the end all to learning SOLID and if you have others that you would recommend instead/as well please do. (And shoot us a link to check it out too!) </p>\n<p>This step should be a complete rewrite and not a refactor of the previous step. It is this way to encourage the pupil to approach the problem from a fresh perspective. </p>\n<p>After the step is completed: </p>\n<ul>\n<li>Ask the pupil if they had any difficulties/why</li>\n<li>Get the pupil to take you through their code and ask them to show it working. </li>\n<li>Compare their new code to their old code side-by-side and have a conversation on how they are different and if they are better/worse.</li>\n<li>Give your pupil tips on how to improve their code further if there is room. </li>\n</ul>\n<p>Notes.</p>\n<ul>\n<li>The solution isn't really complex enough to justify every aspect of SOLID programming at this stage, so your pupils may struggle with finding a reason to use them. This is fine and if there isn't a good reason to use a certain attribute, don't. </li>\n</ul>\n<h2>Problem:</h2>\n<h2>S.O.L.I.D</h2>\n<ul>\n<li>Single Responsibility</li>\n<li>Open/Closed</li>\n<li>Liskov Substitution</li>\n<li>Interface Segregation</li>\n<li>Dependency Inversion</li>\n</ul>\n<p><a href=\"https://en.wikipedia.org/wiki/SOLID_(object-oriented_design)\">https://en.wikipedia.org/wiki/SOLID<em>(object-oriented</em>design)</a></p>\n<p><a href=\"https://www.codeproject.com/Articles/703634/SOLID-architecture-principles-using-simple-Csharp\">https://www.codeproject.com/Articles/703634/SOLID-architecture-principles-using-simple-Csharp</a></p>\n<h2>Task:</h2>\n<p>Read up on the SOLID design principles and then re-implement the console app shopping cart, with an extension.</p>\n<h3>Extension - Shipping cost.</h3>\n<p>Depending on the item weight, there will be several brackets the weight falls into:\nWeight Bracket: $Cost</p>\n<ul>\n<li>0g - 75g: $3.50</li>\n<li>76g - 200g: $8</li>\n<li>200g - 499g: $15</li>\n<li>500g - 999g: $22</li>\n<li>1kg - 2.999kg: $35</li>\n<li>3kg - 8kg: $50</li>\n<li>8kg+: 50 + $5/kg over 8kg</li>\n</ul>\n<p>Calculate the shipping cost and add that to the total.</p>\n<p><a href=\"../shopping\">&#x3C; Prev</a> | <a href=\"../TDD\">Next ></a></p>","frontmatter":{"path":"/shopping2","title":"Shopping Cart 2: S.O.L.I.D"}}},"pathContext":{}}

